$darkblue: #0e3a73;
$lightblue: #1d5399;
$glow: #5270ca;

.custom-button {
	display: inline-block;
	margin: 5px;
	-webkit-box-sizing:border-box;
	   -moz-box-sizing:border-box;
	        box-sizing:border-box;
	min-width:100px;
	padding: 22px 33px;
	font-size: 3em;
	line-height: 46px;
	text-decoration: none;
	color: #FFF;
	text-shadow: 0 1px 2px rgba(0,0,0,0.75);
	outline: none;
	border-radius: 15px;
	border: 1px solid $lightblue;
	box-shadow:
		inset 1px 1px 0px rgba(255,255,255,0.25), /* highlight */
		inset 0 0 6px $glow, /* inner glow */
		inset 0 80px 80px -40px $darkblue, /* gradient */
		1px 1px 3px rgba(0,0,0,0.75); /* shadow */
		
	position: relative;
	overflow: visible; /* IE9 & 10 */
	-webkit-transition: 500ms linear;
	   -moz-transition: 500ms linear;
	     -o-transition: 500ms linear;
					transition: 500ms linear;

    &:before {
        content: '';
        display: block;
        position: absolute;
        top: -7px;
        left: -3px;
        right: 0;
        height: 23px;
        background: url(data:image/png;base64,) no-repeat 0 0,
        url(data:image/png;base64,) no-repeat 50% 0,
        url(data:image/png;base64,) no-repeat 100% 0;
        
    }

    &:hover {
    background: $darkblue;
	cursor: pointer;
	text-shadow: 0 1px 2px rgba(0,0,0,0.75), 0 0 40px #FFF;
	box-shadow:
		inset 1px 1px 0px rgba(255,255,255,0.25), /* highlight */
		inset 0 0 6px $lightblue, /* inner glow */
		inset 0 80px 80px -40px $darkblue, /* gradient */
		1px 1px 3px rgba(0,0,0,0.75); /* shadow */
    }

    &:focus {
        outline: none; /*FF*/
    }
}


